import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { NoResultsSearch } from "@rp/react/web/icons"
import { Box, Typography } from "@rp/react/web/mui/core"

const NoDatasetsPlaceholder = () => {
  const { isMobile } = useDeviceInfo()

  if (isMobile) {
    return (
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          pt: 14,
          alignItems: "center",
          textAlign: "center",
        }}
      >
        <Box
          sx={{
            borderRadius: "100%",
            backgroundColor: "blue.900",
            width: 197,
            height: 197,
            display: "flex",
          }}
        >
          <NoResultsSearch height={135} width={135} style={{ margin: "auto" }} />
        </Box>
        <Typography sx={{ fontSize: 20, mt: 4 }}>No results found for your search</Typography>
        <Typography sx={{ color: "grey.300" }}>
          We couldn't find any result for your search. Try something different.
        </Typography>
      </Box>
    )
  }

  return (
    <Box sx={{ display: "flex", pt: 4.5, pb: 3.5, px: 2.5 }}>
      <Box>
        <NoResultsSearch />
      </Box>
      <Box>
        <Typography>No results for your search</Typography>
        <Typography sx={{ color: "text.secondary" }}>
          We couldn't find any result for your search. Try something different.
        </Typography>
      </Box>
    </Box>
  )
}
export { NoDatasetsPlaceholder }
